@charset "UTF-8";
*,
*::before,
*::after {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body,
h1,
h2,
h3,
h4,
h5,
p,
span,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
}

ul,
ol {
  list-style: none;
}

a:not([class]) {
  text-decoration-skip-ink: none;
  text-decoration: none;
}

a {
  text-decoration-skip-ink: none;
  text-decoration: none;
}

img {
  display: block;
}

article > * + * {
  margin-top: 1em;
}

input,
button,
textarea,
select {
  margin: 0;
  padding: 0;
  outline: none;
}

.container {
  max-width: 1144px;
  margin: 0 auto;
}
@media (max-width: 1199px) {
  .container {
    padding: 0 20px;
  }
}

html,
body {
  font-family: "M PLUS 1p", serif;
  background-color: #fcfcfc;
}

.more {
  background: #f61067;
  height: 45px;
  padding: 0 45px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fcfcfc;
  width: fit-content;
  border-radius: 36px;
  letter-spacing: 2px;
  text-transform: uppercase;
  font-size: 13px;
  transition: 0.3s;
  opacity: 1;
}
.more:hover {
  opacity: 0.8;
  transition: 0.3s;
}

.hero {
  padding: 255px 0 150px;
  background-image: url(/images/hero-bg.webp);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center bottom;
  position: relative;
}
@media (max-width: 1199px) {
  .hero {
    padding: 200px 0 150px;
  }
}
.hero .container {
  position: relative;
}
.hero .sub-title {
  color: #fcfcfc;
  margin-bottom: 18px;
  text-transform: uppercase;
  letter-spacing: 4px;
}
.hero .title {
  font-size: 99px;
  font-style: normal;
  font-weight: 900;
  line-height: 118.8%;
  display: flex;
  flex-direction: column;
  color: #fcfcfc;
}
.hero .title span {
  color: #f61067;
}
@media (max-width: 768px) {
  .hero .title {
    font-size: 79px;
  }
}
.hero .info {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 40px;
  padding: 35px;
  border: 1px solid rgba(255, 255, 255, 0.25);
  border-left: none;
  max-width: 57%;
  margin-top: 50px;
  cursor: pointer;
}
@media (max-width: 768px) {
  .hero .info {
    gap: 10px;
    padding: 10px;
    max-width: 90%;
  }
}
.hero .info__text {
  color: #fcfcfc;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 163.35%;
}
.hero .rotated {
  position: absolute;
  right: 0;
  top: 50%;
  display: flex;
  transform: translate(100%, -50%) rotate(90deg);
  transform-origin: left top;
  display: flex;
  align-items: center;
}
@media (max-width: 1199px) {
  .hero .rotated {
    display: none;
  }
}
.hero .rotated__text {
  color: #fcfcfc;
  font-size: 12px;
  font-style: normal;
  font-weight: 800;
  letter-spacing: 6px;
  text-transform: uppercase;
}
.hero .rotated img {
  width: 21px;
  height: 46px;
  transform: rotate(-90deg);
  margin-left: 50px;
}

.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 2;
  padding: 72px 0;
}
@media (max-width: 1199px) {
  .header {
    padding: 30px 0;
  }
}
.header .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header .wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 100px;
}
@media (max-width: 768px) {
  .header .wrap {
    gap: 15px;
  }
}
@media (max-width: 768px) {
  .header .nav {
    width: 0;
    overflow: hidden;
  }
}
@media (max-width: 768px) {
  .header .nav.open {
    position: absolute;
    top: 100px;
    left: 50%;
    transform: translate(-50%);
    z-index: 2;
    width: 100%;
  }
}
.header .nav__list {
  display: flex;
  gap: 54px;
  padding: 0;
}
@media (max-width: 1199px) {
  .header .nav__list {
    gap: 20px;
  }
}
@media (max-width: 768px) {
  .header .nav__list {
    justify-content: center;
  }
}
.header .nav__list__item {
  padding-bottom: 7px;
  position: relative;
}
.header .nav__list__item a {
  color: #fcfcfc;
  font-size: 14px;
  font-weight: 800;
  letter-spacing: 2px;
  opacity: 0.8;
  transition: 0.3s;
}
.header .nav__list__item.active a {
  opacity: 1;
  transition: 0.3s;
}
.header .nav__list__item.active::after {
  content: "";
  height: 1px;
  background-color: #fcfcfc;
  width: 60%;
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
}
.header .phone {
  display: flex;
  align-items: center;
  gap: 18px;
  font-size: 14px;
  font-style: normal;
  font-weight: 800;
  line-height: 208%;
  letter-spacing: 2px;
  color: #fcfcfc;
}
@media (max-width: 768px) {
  .header .phone {
    gap: 5px;
    letter-spacing: 0px;
  }
}
.header .bugrer {
  flex-direction: column;
  align-items: flex-end;
  gap: 5px;
  width: 25px;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  display: none;
}
@media (max-width: 768px) {
  .header .bugrer {
    display: flex;
  }
}
@media (max-width: 768px) {
  .header .bugrer.open {
    display: flex;
  }
  .header .bugrer.open .line:nth-last-child(1) {
    display: none;
  }
  .header .bugrer.open .line:nth-child(1) {
    transform: rotate(45deg);
  }
  .header .bugrer.open .line:nth-child(2) {
    transform: rotate(-45deg);
    position: relative;
    top: -7px;
  }
}
.header .bugrer .line {
  height: 2px;
  width: 100%;
  background-color: #fcfcfc;
}
.header .bugrer .line:nth-last-child(1) {
  width: 66.666%;
}

.about-us {
