  }
}
.about-us .container .img-block .img-wrap .rectangle-sm {
  position: absolute;
  right: -1px;
  top: -1px;
  background: #30124e;
  height: 511px;
  width: 130px;
}
@media (max-width: 1199px) {
  .about-us .container .img-block .img-wrap .rectangle-sm {
    display: none;
  }
}
.about-us .container .img-block .img-wrap img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.about-us .container .img-block .rectangle {
  position: absolute;
  right: 0;
  top: -30px;
  transform: translateX(50%);
  z-index: 1;
  width: 441px;
  height: 540px;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  background: #30124e;
}
@media (max-width: 1199px) {
  .about-us .container .img-block .rectangle {
    display: none;
  }
}
.about-us .container .img-block .rectangle__text {
  font-family: "Noticia Text", serif;
  font-weight: 400;
  font-style: italic;
  font-size: 22.5px;
  font-weight: 400;
  line-height: 177.417%;
  color: #fcfcfc;
  max-width: 255px;
  z-index: 3;
  position: absolute;
  right: -190px;
  top: 200px;
}
@media (max-width: 1199px) {
  .about-us .container .img-block .rectangle__text {
    display: none;
  }
}
.about-us .container .img-block .rectangle__text::after {
  content: "“";
  opacity: 0.25;
  font-size: 144px;
  font-style: italic;
  font-weight: 700;
  line-height: 150.122%;
  position: absolute;
  left: 50%;
  bottom: 0px;
  transform: translate(-55%, -30px);
}
.about-us .container .img-block .rectangle__text::before {
  content: "”";
  opacity: 0.25;
  font-size: 144px;
  font-style: italic;
  font-weight: 700;
  line-height: 150.122%;
  position: absolute;
  left: 50%;
  top: 0px;
  transform: translate(-55%, 90px);
}

